* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.App {
  width: 100%;
  height: 100vh;
  background-color: #6c63ff;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px 10px;
}

.container {
  position: relative;
}

.button {
  position: absolute;
  left: 30%;
  bottom: 50px;
  padding: 10px 30px;
  background-color: lightcoral;
  border-radius: 25px;
  font-size: 1.4rem;
  color: #fff;
  cursor: pointer;
  user-select: none;
  font-family: "Poppins", sans-serif;
  transition: transform 0.3s ease;
}
.button:active {
  transform: scale(1.1);
}

.box {
  padding: 10px;
  background-color: #e7e7e7;
  border-radius: 10px;
  font-size: 1.2rem;
  font-family: 'Ma Shan Zheng', cursive;
  margin-bottom: 10px;
}

.lists {
  width: 78%;
  height: 75%;
  position: absolute;
  left: 30px;
  top: 60px;
  overflow-y: scroll;
  display: flex;
  flex-direction: column;
}
.lists::-webkit-scrollbar {
   display: none;
}
